import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			address: {
				realName: "",
				phone: "",
				province: "",
				city: "",
				district: "",
				detail: "",
				postCode: "",
				default: null
			},
			editType: 'edit',
			isDefault: false,
			cityPickerValue: [0, 0, 1],
			themeColor: '#ffc107'
		};
	},
	methods: {
		onCancel(e) {
			console.log(e)
		},
		chooseCity() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.region = e;
			console.log(e)
			let labels = e.label.split("-")
			this.address.province = labels[0]
			this.address.city = labels[1]
			this.address.district = labels[2]
		},
		isDefaultChange(e) {
			this.address.default = e.detail.value;
		},
		selectLocation() {
			var that = this;
			uni.chooseLocation({
				success: function(res) {
					// 点击确定后成功回调返回的信息
					console.log(res)
					let addressRegex = /.+?(省|市|自治区|自治州|县|区)/g;
					let provinceCityAndDistrict = res.address.match(addressRegex)
					that.address.province = provinceCityAndDistrict[0]
					
					if (provinceCityAndDistrict.length < 3) {
						that.address.city = provinceCityAndDistrict[0]
						that.address.district = provinceCityAndDistrict[1]
					} else {
						that.address.city = provinceCityAndDistrict[1]
						that.address.district = provinceCityAndDistrict[2]
					}

					that.address.detail = res.address + res.name;
					that.address.latitude = res.latitude // 经度
					that.address.longitude = res.longitude // 纬度
				},
			})
		},
		del() {
			let that = this
			uni.showModal({
				title: '确定删除？',
				content: '你将删除此收货地址，此操作不可撤销。是否确定继续？',
				success: (res) => {
					if (res.confirm) {
						that.deleteAddress()
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});

		},
		save() {
			if (this.editType != 'edit') {
				// 创建模式下ID为空
				this.address.id = null
			}
			if (!this.address.realName) {
				uni.showToast({
					title: '请输入收件人姓名',
					icon: 'none'
				});
				return;
			}
			if (!this.address.phone) {
				uni.showToast({
					title: '请输入收件人电话号码',
					icon: 'none'
				});
				return;
			}
			if (!this.address.detail) {
				uni.showToast({
					title: '请输入收件人详细地址',
					icon: 'none'
				});
				return;
			}
			if (this.address.province == '' || this.address.city == '' || this.address.district == '') {
				uni.showToast({
					title: '请选择收件省市区(县)',
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '正在保存',
				mask: true
			})
			// 实际应用中请提交ajax,模板定时器模拟提交效果
			this.saveAddress()
		},
		saveAddress() {
			let that = this
			this.$pin.request(this.editType == 'edit' ? 'PUT' : 'POST', '/commons/user/address', this.address,
				successData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'success',
						title: '保存地址成功'
					})
					uni.navigateBack(-1)
				},
				failData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: '保存地址时出错'
					})
				}
			)
		},
		deleteAddress() {
			uni.showLoading({
				title: '正在删除',
				mask: true
			})
			let that = this
			this.$pin.request('DELETE', '/commons/user/address', this.address,
				successData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'success',
						title: '已删除地址'
					})
					uni.navigateBack(-1)
				},
				failData => {
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: '删除地址时出错'
					})
				}
			)
		}
	},
	onLoad(e) {
		//获取传递过来的参数
		this.editType = e.type;
		if (e.type == 'edit') {
			uni.getStorage({
				key: 'address',
				success: (e) => {
					this.address = e.data
				}
			})
		}

	},
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
};
